
































import Vue, { PropType } from 'vue'
import AppButton from '@/ui/AppButton.vue'
import AppModal from '@/ui/AppModal.vue'
import { IBlock } from '@/types/block.type'
import BlockCrudFrom from '@/components/block/BlockCrudFrom.vue'

export default Vue.extend({
  name: 'BlockEdit',
  components: {
    BlockCrudFrom,
    AppModal,
    AppButton
  },
  props: {
    value: {
      type: Boolean,
      required: true
    },
    block: {
      type: Object as PropType<IBlock>,
      required: true
    }
  },
  methods: {
    modalDisplayChange (value: boolean): void {
      this.$emit('input', value)
    },
    saveBlockData (block: IBlock): void {
      this.$emit('input', false)
      this.$emit('editBlock', block)
    }
  }
})
