

















import Vue from 'vue'
import SvgItem from '@/components/svg/SvgItem.vue'

export default Vue.extend({
  name: 'AppModal',
  components: { SvgItem },
  props: {
    value: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {}
  },
  methods: {
    show () {
      this.$emit('input', true)
    },
    hide () {
      this.$emit('input', false)
    }
  }
})
