




















import Vue, { PropType } from 'vue'
import SvgItem from '@/components/svg/SvgItem.vue'
export default Vue.extend({
  name: 'AppButton',
  components: { SvgItem },
  props: {
    iconId: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String as PropType<'button' | 'submit' | 'reset'>,
      default: 'button'
    }
  }
})
